<template>Â 
  <div class="app-container">
    <el-card class="filter-container" shadow="never">
      <div>
        <i class="el-icon-search"></i>
        <span>ç­éæç´¢</span>
        <el-button
          style="float:right"
          type="primary"
          @click="handleSearchList()"
          size="small">
          æ¥è¯¢æç´¢
        </el-button>
        <el-button
          style="float:right;margin-right: 15px"
          @click="handleResetSearch()"
          size="small">
          éç½®
        </el-button>
      </div>
      <div style="margin-top: 15px">
        <el-form :inline="true" :model="listQuery" size="small" label-width="140px">
          <el-form-item label="åååç§°ï¼">
            <el-input v-model="listQuery.productName" class="input-width" placeholder="åååç§°"></el-input>
          </el-form-item>
          <el-form-item label="æ¨èç¶æï¼">
            <el-select v-model="listQuery.recommendStatus" placeholder="å¨é¨" clearable class="input-width">
              <el-option v-for="item in recommendOptions"
                         :key="item.value"
                         :label="item.label"
                         :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <el-card class="operate-container" shadow="never">
      <i class="el-icon-tickets"></i>
      <span>æ°æ®åè¡¨</span>
      <el-button size="mini" class="btn-add" @click="handleAdd()">æ·»å </el-button>
    </el-card>
    <div class="table-container">
      <el-table ref="newProductTable"
                :data="list"
                style="width: 100%;"
                @selection-change="handleSelectionChange"
                v-loading="listLoading" border>
        <el-table-column type="selection" width="60" align="center"></el-table-column>
        <el-table-column label="ç¼å·" width="120" align="center">
          <template slot-scope="scope">{{scope.row.ruleId}}</template>
        </el-table-column>
        <el-table-column label="ååç¼å·" width="120" align="center">
          <template slot-scope="scope">{{scope.row.productId}}</template>
        </el-table-column>
        <el-table-column label="åååç§°" align="center">
          <template slot-scope="scope">{{scope.row.productName}}</template>
        </el-table-column>
        <el-table-column label="å¼å§æ¶é´" width="170" align="center">
          <template slot-scope="scope">{{scope.row.startTime|formatDate}}</template>
        </el-table-column>
        <el-table-column label="ç»ææ¶é´" width="170" align="center">
          <template slot-scope="scope">{{scope.row.endTime|formatDate}}</template>
        </el-table-column>
        <el-table-column label="ç»å¢äººæ°" width="160" align="center">
          <template slot-scope="scope">{{scope.row.limitPersonNumber}}</template>
        </el-table-column>
        <el-table-column label="åå®æ°é" width="160" align="center">
          <template slot-scope="scope">{{scope.row.provideNumber}}</template>
        </el-table-column>
        <el-table-column label="ç¶æ" width="160" align="center">
          <template slot-scope="scope">{{scope.row.endTime | formatStatus}}</template>
        </el-table-column>
        <el-table-column label="æä½" width="180" align="center">
          <template slot-scope="scope">
            <el-button size="mini"
                       type="text"
                       @click="handleEdit(scope.$index, scope.row)">ç¼è¾
            </el-button>
            <el-button size="mini"
                       type="text"
                       @click="handleDelete(scope.$index, scope.row)">å é¤
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="batch-operate-container">
      <el-select
        size="small"
        v-model="operateType" placeholder="æ¹éæä½">
        <el-option
          v-for="item in operates"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
      <el-button
        style="margin-left: 20px"
        class="search-button"
        @click="handleBatchOperate()"
        type="primary"
        size="small">
        ç¡®å®
      </el-button>
    </div>
    <div class="pagination-container">
      <el-pagination
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        layout="total, sizes,prev, pager, next,jumper"
        :page-size="listQuery.pageSize"
        :page-sizes="[5,10,15]"
        :current-page.sync="listQuery.pageNum"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>
<script>
  import {fetchList,updateStatus,deleteGroupon,createGroupon} from '@/api/groupon';
  import {formatDate} from '@/utils/date';
  const defaultListQuery = {
    pageNum: 1,
    pageSize: 5,
    productName: null,
    recommendStatus: null
  };

  const defaultRecommendOptions = [
    {
      label: 'æªæ¨è',
      value: 0
    },
    {
      label: 'æ¨èä¸­',
      value: 1
    }
  ];
  export default {
    name: 'newProductList',
    data() {
      return {
        listQuery: Object.assign({}, defaultListQuery),
        recommendOptions: Object.assign({}, defaultRecommendOptions),
        list: null,
        total: null,
        listLoading: false,
        multipleSelection: [],
        operates: [
          {
            label: "è®¾ä¸ºæ¨è",
            value: 0
          },
          {
            label: "åæ¶æ¨è",
            value: 1
          },
          {
            label: "å é¤",
            value: 2
          }
        ],
        operateType: null,
        selectDialogVisible:false,
        dialogData:{
          list: null,
          total: null,
          multipleSelection:[],
          listQuery:{
            keyword: null,
            pageNum: 1,
            pageSize: 5
          }
        }
      }
    },
    created() {
      this.getList();
    },
    filters:{
      formatRecommendStatus(status){
        if(status===1){
          return 'æ¨èä¸­';
        }else{
          return 'æªæ¨è';
        }
      },
        formatDate(time){
            if(time==null||time===''){
                return 'N/A';
            }
            let date = new Date(time);
            return formatDate(date, 'yyyy-MM-dd hh:mm:ss')
        },
        formatStatus(endTime){
            let now = new Date();
             now = formatDate(now,'yyyy-MM-dd hh:mm:ss');
            if(endTime < now){
                return 'å·²ç»æ'
            }else{
                return 'æªç»æ';
            }
        }
    },
    methods: {
      handleResetSearch() {
        this.listQuery = Object.assign({}, defaultListQuery);
      },
      handleSearchList() {
        this.listQuery.pageNum = 1;
        this.getList();
      },
      handleSelectionChange(val){
        this.multipleSelection = val;
      },
      handleSizeChange(val) {
        this.listQuery.pageNum = 1;
        this.listQuery.pageSize = val;
        this.getList();
      },
      handleCurrentChange(val) {
        this.listQuery.pageNum = val;
        this.getList();
      },
     handleRecommendStatusStatusChange(index,row){
        this.updateRecommendStatusStatus(row.id,row.recommendStatus);
      },
      handleDelete(index,row){
        this.deleteProduct(row.id);
      },
      handleBatchOperate(){
        if (this.multipleSelection < 1) {
          this.$message({
            message: 'è¯·éæ©ä¸æ¡è®°å½',
            type: 'warning',
            duration: 1000
          });
          return;
        }
        let ids = [];
        for (let i = 0; i < this.multipleSelection.length; i++) {
          ids.push(this.multipleSelection[i].id);
        }
        if (this.operateType === 0) {
          //è®¾ä¸ºæ¨è
          this.updateRecommendStatusStatus(ids,1);
        } else if (this.operateType === 1) {
          //åæ¶æ¨è
          this.updateRecommendStatusStatus(ids,0);
        } else if(this.operateType===2){
          //å é¤
          this.deleteProduct(ids);
        }else {
          this.$message({
            message: 'è¯·éæ©æ¹éæä½ç±»å',
            type: 'warning',
            duration: 1000
          });
        }
      },
        handleAdd(){
            this.$router.push({path: '/ums/addGroupon'})
        },
      handleSelectSearch(){
        this.getDialogList();
      },
      handleDialogSizeChange(val) {
        this.dialogData.listQuery.pageNum = 1;
        this.dialogData.listQuery.pageSize = val;
        this.getDialogList();
      },
      handleDialogCurrentChange(val) {
        this.dialogData.listQuery.pageNum = val;
        this.getDialogList();
      },
      handleDialogSelectionChange(val){
        this.dialogData.multipleSelection = val;
      },
      handleSelectDialogConfirm(){
        if (this.dialogData.multipleSelection < 1) {
          this.$message({
            message: 'è¯·éæ©ä¸æ¡è®°å½',
            type: 'warning',
            duration: 1000
          });
          return;
        }
        let selectProducts = [];
        for (let i = 0; i < this.dialogData.multipleSelection.length; i++) {
          selectProducts.push({
            productId:this.dialogData.multipleSelection[i].id,
            productName:this.dialogData.multipleSelection[i].name
          });
        }
        this.$confirm('ä½¿ç¨è¦è¿è¡æ·»å æä½?', 'æç¤º', {
          confirmButtonText: 'ç¡®å®',
          cancelButtonText: 'åæ¶',
          type: 'warning'
        }).then(() => {
          createGroupon(selectProducts).then(response=>{
            this.selectDialogVisible=false;
            this.dialogData.multipleSelection=[];
            this.getList();
            this.$message({
              type: 'success',
              message: 'æ·»å æå!'
            });
          });
        });
      },
      handleEdit(index,row){
          this.$router.push({path: '/ums/updateGroupon', query: {id: row.ruleId}})
      },
      getList() {
        this.listLoading = true;
        fetchList(this.listQuery).then(response => {
          this.listLoading = false;
          this.list = response.data;
          this.total = response.total;
        })
      },
      updateRecommendStatusStatus(ids,status){
        this.$confirm('æ¯å¦è¦ä¿®æ¹æ¨èç¶æ?', 'æç¤º', {
          confirmButtonText: 'ç¡®å®',
          cancelButtonText: 'åæ¶',
          type: 'warning'
        }).then(() => {
          let params=new URLSearchParams();
          params.append("ids",ids);
          params.append("recommendStatus",status);
          updateStatus(params).then(response=>{
            this.getList();
            this.$message({
              type: 'success',
              message: 'ä¿®æ¹æå!'
            });
          });
        }).catch(() => {
          this.$message({
            type: 'success',
            message: 'å·²åæ¶æä½!'
          });
          this.getList();
        });
      },
      deleteProduct(ids){
          console.log(ids)
        this.$confirm('æ¯å¦è¦å é¤è¯¥æ¨è?', 'æç¤º', {
          confirmButtonText: 'ç¡®å®',
          cancelButtonText: 'åæ¶',
          type: 'warning'
        }).then(() => {
          //let params=new URLSearchParams();
         // params.append("ids",ids);
          deleteGroupon(ids).then(response=>{
            this.getList();
            this.$message({
              type: 'success',
              message: 'å é¤æå!'
            });
          });
        })
      },
      getDialogList(){
        fetchProductList(this.dialogData.listQuery).then(response=>{
          this.dialogData.list=response.data;
          this.dialogData.total=response.total;
        })
      },
    }
  }
</script>
